:root {
  /* Color Palette - Dark & Neon */
  --bg-primary: #0a0c10;
  /* Deep Slate/Black */


  --bg-secondary: #161b22;
  /* Slightly lighter for cards */
  --bg-glass: rgba(22, 27, 34, 0.85);
  /* Glassmorphism base - Increased opacity for readability */

  --text-primary: #ffffff;
  --text-secondary: #d1d5db;
  /* Lighter grey (Tailwind gray-300) for better contrast */
  --text-accent: #58a6ff;
  /* Neon Blue */

  --accent-primary: #58a6ff;
  /* Blue */
  --accent-secondary: #bc8cff;
  /* Purple */
  --accent-success: #3fb950;
  /* Green */
  --accent-danger: #f85149;
  /* Red */

  --border-color: #30363d;
  --border-hover: #8b949e;

  --shadow-sm: 0 1px 0 rgba(27, 31, 35, 0.04);
  --shadow-md: 0 3px 6px rgba(0, 0, 0, 0.4);
  --shadow-glow: 0 0 15px rgba(88, 166, 255, 0.4);
}

[data-theme="dark"] {
  --bg-primary: #0a0c10;
  --bg-secondary: #161b22;
  --bg-glass: rgba(22, 27, 34, 0.85);
  --text-primary: #ffffff;
  --text-secondary: #d1d5db;
  --border-color: #30363d;
  --border-hover: #8b949e;
  --shadow-sm: 0 1px 0 rgba(27, 31, 35, 0.04);
  --shadow-md: 0 3px 6px rgba(0, 0, 0, 0.4);
  --shadow-glow: 0 0 15px rgba(88, 166, 255, 0.3);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: var(--font-sans);
  background-color: var(--bg-primary);
  color: var(--text-primary);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
  background-image:
    linear-gradient(rgba(48, 54, 61, 0.1) 1px, transparent 1px),
    linear-gradient(90deg, rgba(48, 54, 61, 0.1) 1px, transparent 1px);
  background-size: 40px 40px;
  /* Tech Grid */
}

html {
  scroll-behavior: smooth;
}

code,
pre {
  font-family: var(--font-mono);
}

/* Global Utility Classes */
.page-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 6rem 2rem 4rem;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.section-title {
  margin-bottom: 3rem;
}

.glass-panel {
  background: var(--bg-glass);
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  border: 1px solid var(--border-color);
  border-radius: 12px;
  box-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.3);
  transition: all 0.3s ease;

  /* Fix for Chrome blurred text */
  transform: translateZ(0);
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  perspective: 1000px;
}

.glass-panel:hover {
  background: rgba(30, 35, 45, 0.9);
  border-color: var(--accent-primary);
  transform: translateY(-2px) translateZ(0);
  box-shadow: var(--shadow-glow);
}

.text-gradient {
  background: linear-gradient(90deg, var(--accent-primary), var(--accent-secondary));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  /* Fix for Chrome rendering */
  transform: translateZ(0);
  display: inline-block;
}

button {
  font-family: var(--font-sans);
  cursor: pointer;
}

/* Enhanced Glassmorphism Utilities (Promoted from Services.css) */
.glass-card {
  background: var(--bg-glass, rgba(22, 27, 34, 0.7));
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 16px;
  padding: 2.5rem 2rem;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  gap: 1.5rem;
  position: relative;
  overflow: hidden;
}

.glass-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 2px;
  background: linear-gradient(90deg, transparent, var(--accent-primary), transparent);
  opacity: 0;
  transition: opacity 0.4s ease;
}

.glass-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 20px 40px -15px rgba(0, 0, 0, 0.5);
  border-color: rgba(88, 166, 255, 0.3);
  background: linear-gradient(145deg, rgba(22, 27, 34, 0.9), rgba(22, 27, 34, 0.6));
}

.glass-card:hover::before {
  opacity: 1;
}

.glass-icon {
  width: 80px;
  height: 80px;
  background: rgba(255, 255, 255, 0.03);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 1.5rem;
  font-size: 2.5rem;
  color: var(--accent-primary);
  transition: all 0.4s ease;
  border: 1px solid rgba(255, 255, 255, 0.05);
}

.glass-card:hover .glass-icon {
  background: rgba(88, 166, 255, 0.1);
  color: #fff;
  transform: scale(1.1) rotate(5deg);
  border-color: rgba(88, 166, 255, 0.3);
  box-shadow: 0 0 20px rgba(88, 166, 255, 0.2);
}

.glass-tag {
  font-size: 0.75rem;
  padding: 0.35rem 0.85rem;
  border-radius: 20px;
  background: rgba(255, 255, 255, 0.03);
  color: var(--text-secondary);
  border: 1px solid rgba(255, 255, 255, 0.08);
  transition: all 0.3s ease;
  font-weight: 500;
  letter-spacing: 0.03em;
}

.glass-card:hover .glass-tag {
  border-color: rgba(88, 166, 255, 0.3);
  background: rgba(88, 166, 255, 0.05);
  color: var(--text-primary);
}

.tags-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 0.5rem;
}

.glass-input {
  background: rgba(255, 255, 255, 0.05) !important;
  border: 1px solid rgba(255, 255, 255, 0.1) !important;
  color: var(--text-primary) !important;
  backdrop-filter: blur(4px);
  transition: all 0.3s ease;
}

.glass-input:focus {
  background: rgba(255, 255, 255, 0.1) !important;
  border-color: var(--accent-primary) !important;
  box-shadow: 0 0 0 2px rgba(88, 166, 255, 0.2) !important;
  outline: none;
}

/* Horizontal Scroll Container */
.horizontal-scroll-container {
  display: flex;
  overflow-x: auto;
  gap: 2rem;
  padding-bottom: 2rem;
  padding-left: 1rem;
  padding-right: 1rem;
  scroll-snap-type: x mandatory;
  -webkit-overflow-scrolling: touch;
}

.horizontal-scroll-container::-webkit-scrollbar {
  height: 8px;
}

.horizontal-scroll-container::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 10px;
}

.horizontal-scroll-container::-webkit-scrollbar-thumb {
  background: var(--accent-primary);
  border-radius: 10px;
}

.horizontal-scroll-container::-webkit-scrollbar-thumb:hover {
  background: var(--accent-secondary);
}

.scroll-item {
  min-width: 350px;
  scroll-snap-align: center;
}

@media (max-width: 768px) {
  .scroll-item {
    min-width: 300px;
  }
}

