  display: inline-block;
  padding: 0.25rem 0.75rem;
  background: rgba(88, 166, 255, 0.15);
  color: var(--accent-primary);
  border: 1px solid rgba(88, 166, 255, 0.3);
  border-radius: 20px;
  font-size: 0.85rem;
  font-weight: 600;
  font-family: var(--font-mono);
}